import React from 'react';
import './App.css';

// CONTENTS ***********************************************
import contents from './contents.json';

const contHeader = {
  title : "SpacetimeQ Labs ",
  sub   : "Data Visualization and Gamification Technologies"
};

const contFooter = {
  url       : "https://test.spacetimeq.com",
  title     : "SpacetimeQ Labs",
  copyright : " (C) 2019",
  mailto    : "mailto:soomin999@gmail.com",
  contact   : " admin"
};

const urlRoot    = "https://soominkimu.github.io/";
const urlContent = urlRoot + "P/";
const urlImages  = urlRoot + "images/";
const imageExt   = ".png";
// CONTENTS ***********************************************

const MasonryHeader = props =>
  <div className="masonry-header">{props.title}
    <span className="masonry-header-sub">{props.sub}</span>
  </div>;

const MasonryFooter = props =>
  <div className="masonry-footer">
    <p>
      <a href={props.url} target="_blank" rel="external noopener noreferrer nofollow">{props.title}</a>
      {props.copyright}
      <a href={props.mailto}>{props.contact}</a>
    </p>
  </div>;

const MasonryItem = props =>
  <div className="masonry-item">
    <div className="masonry-content">
      <a href={urlContent + props.content} target="_blank" rel="noopener noreferrer">
        <img src={urlImages + props.content + imageExt} alt={props.alt} />
      </a>
      <h3 className="masonry-title">{props.title}
        {props.nb && <span className="masonry-nb">{" " + props.nb}</span>}
      </h3>
      <p className="masonry-description">{props.desc}</p>
    </div>
  </div>;

// ES2018 property spread notation {...props}
const App = () => {
  let lns = [];
  let id=0;
  contents.forEach(cont =>
    lns.push( <MasonryItem key = {id++} {...cont} /> )
  );
  //console.log(id, "items");

  return (
    <div className="App">
      <MasonryHeader {...contHeader} />
      <div className="masonry-wrapper">
        <div className="masonry">{lns}</div>
      </div>
      <MasonryFooter {...contFooter} />
    </div>
  );
}

export default App;
